import axios from 'axios'
import _ from 'lodash'

const getAllContacts = async(offset)=>{
    try {
        
        //let finilizar = true
        const off = offset !== "N" ? offset : "N"
        const url = `api/contact/${off}`
        const data = await axios.get((process.env.REACT_APP_MOCK_THIS_IS_NOT_A_KEY+ '' +url))
        const dataVal = _.values(data);
        let dataFormat = await Promise.all(_.map(data.data.contacts, async (item)=>{
            let p = item.properties;
            let firstName = p.firstname ? p.firstname.value : "NA";
            let lastName = p.lastname ? p.lastname.value : "NA";
            let email = p.email ? p.email.value : "NA";
            let phone = p.phone ? p.phone.value : "NA";
            let source = p.source ? p.source.value : "NA";
            let postalCode = p.zip ? p.zip.value : "NA";
            let stateName = p.state ? p.state.value : "NA";
            let city = p.city ? p.city.value : "NA";
            let address = p.address ? p.address.value: "NA";
            let company = p.company ? p.company.value : "NA";
            let mobilephone = p.mobilephone ? p.mobilephone.value : "NA";
            let hubspot_owner_id = p.hubspot_owner_id ? p.hubspot_owner_id.value : "NA";
            let ownerName = "NA";
            let lastModified = p.lastmodifieddate ? new Date(parseInt(p.lastmodifieddate.value)).toDateString() : "NA";
            let contactId = item.vid ? item.vid : "NA";
            let callCenterID = p.call_center_id ? p.call_center_id.value : "NA";
            let callCenterAgent = p.call_center_agent ? p.call_center_agent.value : "NA";
            let appointment = p.appointment ? new Date(parseInt(p.appointment.value)) : "NA";
            let time = p.time ? p.time.value : "NA";
            let notes = p.notes ? p.notes.value : "NA";
            let preferredMethod = p.preferred_consultation_method ? p.preferred_consultation_method.value : "NA";
            let language = p.hs_language ? p.hs_language.value : "NA";
            let qualified_appointment = p.qualified_appointment ? p.qualified_appointment.value : "NA";
            let audio = p.audio_recording ? p.audio_recording.value : "NA";
            let dealId = await getDealId(contactId);
            let dealName = "NA"
            let stage
            if(hubspot_owner_id !== "NA"){
                ownerName = await getDealById(hubspot_owner_id)
            }
            if(dealId !== undefined && dealId !== 'NA'){
               await getDealIdData(dealId).then((i)=>{
                  dealName = i.name
                  stage = i.stage
               })
            }

            return({
                firstName: firstName,
                lastName: lastName,
                city:city,
                address:address,
                stateName:stateName,
                postalCode:postalCode,
                email: email,
                phone: phone,
                source: source,
                company: company,
                mobilephone: mobilephone,
                hubspot_owner_id: hubspot_owner_id,
                ownerName: ownerName !== "NA" ? `${ownerName}`: ownerName,
                contactId: contactId,
                dealId: dealId !== undefined ? dealId : "NA",
                stage: stage !== undefined ? stage : "NA",
                dealName: dealName !== undefined ? dealName : "NA",
                callCenterID:callCenterID,
                callCenterAgent:callCenterAgent,
                lastModified:lastModified,
                appointment:appointment,
                time:time,
                notes:notes,
                preferredMethod:preferredMethod,
                language:language,
                qualified_appointment:qualified_appointment,
                audio:audio,
                item: _.values(dataVal[0])
            });
        }));
        return dataFormat
}
    catch(e){
        console.log(e)
    }
}

const getDealById = async(id)=>{
    const url = `api/deals/dealById/${id}`
    const data = await axios.get((process.env.REACT_APP_MOCK_THIS_IS_NOT_A_KEY+ '' +url))
    return `${data.data.firstName ? data.data.firstName : "NA" } ${data.data.lastName ? data.data.lastName : "" }`
}

const getDealId = async(id)=>{
    if(id){
    const url = `api/deals/getIdDeal/${id}`
    const data = await axios.get((process.env.REACT_APP_MOCK_THIS_IS_NOT_A_KEY+ '' +url))
    return (data.data.results !== undefined ? data.data.results[0] : "NA")
    }
}

const getDealIdData = async(id)=>{
    if(id){
        const url = `api/deals/getIdDealData/${id}`
        const data = await axios.get((process.env.REACT_APP_MOCK_THIS_IS_NOT_A_KEY+ '' +url))
        if(data.data.properties !== undefined){
        return({name: data.data.properties.dealname !== undefined ? data.data.properties.dealname.value : "NA", stage: data.data.properties.dealstage !== undefined ? data.data.properties.dealstage.value : "NA"})
        }
        return ({name: 'NA', stage: "NA"})
    }
}


export {
    getAllContacts
}